html {
  --mdc-typography-subtitle1-line-height: 1.2;
}

.App {
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  font-family:  "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a{
  text-decoration: none;
  color: #653f3f;
}

a:hover, .menuContainer a:hover {
  color: #cf8080;
  font-weight: bold;
}

a:visited {
  color: #a13d3d;
}

.contextA{
  color: #000000;
  text-decoration: none;
}

.contextA:hover {
  color: #cf8080;
  font-weight: normal;
}

.contextA:visited {
  color: #555555;
}

a:visited {
  color: #a13d3d;
}

.MuiTypography-body1 {
  font-family: 'Gravity';
}

.menuContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: fixed;
  right: 0px;
  top: 0px;
  z-index: 100;
  padding: 70px 40px 40px 40px;
}

.menuContainer a {
  cursor: pointer;
  color: #cecece;
}

.menuIcon {
  cursor: pointer;
}

.menuIcon:hover {
  color: #cf8080;
  font-weight: bold;
}